<template>
  <Layout :has-nav="false">
    <div class="password-login">
      <div class="password-form">
        <h1>找回密码</h1>
        <div class="input-wrapper">
          <input type="tel"
                 placeholder="请输入注册时使用的手机号"
                 maxlength="11"
                 v-model="phoneNumber">
          <p>
            <input type="tel"
                   placeholder="请输入验证码"
                   maxlength="6"
                   v-model="verificationCode">
            <button :disabled="checkPhoneNumber"
                    @click="getVerificationCode">
              {{ verificationCodeText }}
            </button>
          </p>
          <input type="password"
                 placeholder="请输入新密码"
                 minlength="6"
                 v-model="password">
          <input type="password"
                 placeholder="再次确认密码"
                 minlength="6"
                 v-model="passwordAgain">
          <button :disabled="check" @click="submit">完成</button>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
export default {
  name: 'PasswordToLogin',
  data() {
    return {
      phoneNumber: '',
      verificationCode: '',
      password: '',
      passwordAgain: '',
      verificationCodeText: '获取验证码',
    }
  },
  computed: {
    checkPhoneNumber: function () {
      if (this.verificationCodeText !== '获取验证码') {
        return true
      }
      if (this.phoneNumber.length !== 11) {
        return true
      } else {
        return !(/^1[3456789]\d{9}$/.test(this.phoneNumber))
      }
    },
    check() {
      if (this.verificationCode.length !== 6) {
        return true
      }
      if (this.password.length < 6) {
        return true
      }
      if (this.password !== this.passwordAgain) {
        return true
      }
    },
  },
  methods: {
    getVerificationCode() {
      console.log('获取验证码')
      this.verificationCodeText = '60s'
      let count = 60
      let timer = setInterval(() => {
        count--
        if (count === 0) {
          clearInterval(timer)
          this.verificationCodeText = '获取验证码'
        } else {
          this.verificationCodeText = `${count}s`
        }
      }, 1000)
    },
    submit() {
    },
  }

}
</script>

